
const SearchSimilar = ({search, data, filter}) => {
        switch (filter) {
            case 2 :
                return data.filter((x) => {
                    if(x.kandungan)
                    return x.kandungan.toLowerCase()?.includes(search.toLowerCase())
                    else return false
                });
            case 3 :
                return data.filter((x) => {
                    if(x.prinsipal)
                    return x.prinsipal.toLowerCase()?.includes(search.toLowerCase())
                    else return false
                });
            case 4 :
                return data.filter((x) => {
                    if(x.indikasi)
                    return x.indikasi.toLowerCase()?.includes(search.toLowerCase())
                    else return false
                });
            default:
                return data.filter((x) => x.name.toLowerCase().includes(search.toLowerCase()))
        }
    
};

export default SearchSimilar;